import { StatusBar } from 'expo-status-bar';
import React, { useEffect, useState } from "react";
import { Platform, StyleSheet, Text, View, TouchableOpacity, TextInput, ScrollView, Alert } from 'react-native';
import { theme } from './color';
import AsyncStorage from '@react-native-async-storage/async-storage';
import {Fontisto} from "@expo/vector-icons";

const STORAGE_KEY = "@toDos";
const SELECT_KEY = "@toSel"
export default function App() {
  const [working, setWorking] = useState(true);
  const travel = async() => {
    setWorking(false);
    await AsyncStorage.setItem(SELECT_KEY, "false");
  }
  const work = async() => {
    setWorking(true);
    await AsyncStorage.setItem(SELECT_KEY, "true");
  }
  const loadToSel = async() => {
    const s = await AsyncStorage.getItem(SELECT_KEY);
    setWorking(s=="true" ? true : false);
  }

  const [text, setText] = useState("");
  const [toDos, setToDos] = useState({});
  const [newText, setNewText] = useState("");
  const [beforeText, setBeforeText] = useState("");

  const deleteToDo = async (key) =>{
    if(Platform.OS === "web"){
      const ok = confirm("삭제하시겠습니까?");
      if(ok){
        const newToDos = {...toDos};
        delete newToDos[key];
        setToDos(newToDos);
        await saveToDos(newToDos);
      }
    }else{
      Alert.alert("Delete To Do","Are you sure?", [
        {text:"Cancel"},
        {text:"Ok",
         onPress:async() =>{
          const newToDos = {...toDos};
          delete newToDos[key];
          setToDos(newToDos);
          await saveToDos(newToDos);
        },
      },
      ]);
    }
    return;
  };

  const successToDo = async (key) =>{
    if(Platform.OS === "web"){
      const ok = confirm(toDos[key].success === true ? "완료를 취소하시겠습니까?" : "완료하시겠습니까?");
      if(ok){
        const newToDos = {...toDos};
        newToDos[key].success === true ? newToDos[key].success = false : newToDos[key].success = true;
        setToDos(newToDos);
        await saveToDos(newToDos);
      }
    }else{
      Alert.alert("Success To Do", toDos[key].success === true ? "완료를 취소하시겠습니까?" : "완료하시겠습니까?",[
        {text:"Cancel"},
        {text: "Ok",
        onPress : async() => {
          const newToDos = {...toDos};
          newToDos[key].success === true ? newToDos[key].success = false : newToDos[key].success = true;
          setToDos(newToDos);
          await saveToDos(newToDos);
        }
      }
      ]);
    }
    return;
  };

  const onChangeCom = async(key) => {
    if(Platform.OS === "web"){
      const ok = confirm("수정완료하시겠습니까?");
      if(ok){
        const newToDos = {...toDos};
        newToDos[key].change = false;
        newToDos[key].text = newText;
        setToDos(newToDos);
        await saveToDos(newToDos);
      }
    }else{
      Alert.alert("Change To Do","수정완료하시겠습니까?",[
        {text:"Cancel"},
        {text: "Ok",
        onPress : async() => {
          const newToDos = {...toDos};
          newToDos[key].change = false;
          newToDos[key].text = newText;
          setToDos(newToDos);
          await saveToDos(newToDos);
        }
      }
      ]);
    }
    return;
  };

  const changeToDo = async(key) => {
    if(Platform.OS === "web"){
      const ok = confirm("수정하시겠습니까?");
      if(ok){
        const newToDos = {...toDos};
        newToDos[key].change = true;
        setToDos(newToDos);
        await saveToDos(newToDos);
      }
    }else{
      Alert.alert("Change To Do","수정하시겠습니까?",[
        {text:"Cancel"},
        {text: "Ok",
        onPress : async() => {
          const newToDos = {...toDos};
          newToDos[key].change = true;
          setToDos(newToDos);
          await saveToDos(newToDos);
        }
      }
      ]);
    }
    return;
  };

  const saveToDos = async(toSave) => {
    await AsyncStorage.setItem(STORAGE_KEY, JSON.stringify(toSave));
  };
  const loadToDos = async() => {
    const s = await AsyncStorage.getItem(STORAGE_KEY);
    if(s == null){
      setToDos({});
    }else{
      setToDos(JSON.parse(s));
    }
  };
  useEffect(() => {
    loadToDos();
    loadToSel();
  }, []);
  const addToDo = async () => {
    if(text === ""){
      return;
    }
    const newToDos = {...toDos, [Date.now()] : {text, working}}
    setToDos(newToDos);
    await saveToDos(newToDos);
    setText("");
  }
  const onChangeText = (payload) => setText(payload);
  const onChangeChangeText = (newText) => setNewText(newText);

  return (
    <View style={styles.container}>
      <StatusBar style="auto" />
      <View style={styles.titleView}> 
        <Text style={styles.title}>MEMO</Text>
      </View>
      <View style={styles.header}>
        <TouchableOpacity onPress={work}>
          <Text style={{...styles.btnsText, color : working ? "white" : theme.grey}}>Work</Text>
        </TouchableOpacity>
        <TouchableOpacity onPress={travel}>
          <Text style={{...styles.btnsText, color : !working ? "white" : theme.grey}}>Travel</Text>
        </TouchableOpacity>
      </View>
      
        <TextInput 
          placeholder={working ? "Add a To Do": "Where do you want to go?"} 
          style={styles.input}
          onChangeText={onChangeText}
          value={text}
          onSubmitEditing={addToDo}
          returnKeyType="done"
        />
        
      <ScrollView>
        {Object.keys(toDos).map(key => (
        toDos[key].working === working ? (
        <View style={styles.toDo} key={key}>
          {toDos[key].change === true ? 
          (
          <TextInput 
            style={styles.changeText}
            onChangeText={onChangeChangeText}
            value={newText}
            onSubmitEditing={() => onChangeCom(key)}
            returnKeyType="done"
          />)
          :
          (<Text style={toDos[key].success === true ? styles.toDoSuc : styles.toDos}>{toDos[key].text}</Text>)}
          <TouchableOpacity style={styles.icon} onPress={() => changeToDo(key)}>
            <Fontisto name="undo" size={18} color={theme.toDoBg}/>
          </TouchableOpacity>
          <TouchableOpacity style={styles.icon} onPress={() => successToDo(key)}>
            <Fontisto name="checkbox-active" size={18} color={theme.toDoBg}/>
          </TouchableOpacity>
          <TouchableOpacity style={styles.icon} onPress={() => deleteToDo(key)}>
            <Fontisto name="trash" size={18} color={theme.toDoBg}/>
          </TouchableOpacity>
        </View>)
        :
        null
      ))}  
      </ScrollView>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: theme.bg,
    paddingHorizontal: 20,
  },
  titleView:{
    marginTop: 70,
    alignItems : "center", 
  },
  title:{
    color : "white",
    fontSize : 50
  },
  header: { 
    justifyContent : "space-between",
    flexDirection: "row",
    marginTop: 40,
  },
  btnsText: {
    fontSize : 38,
    fontWeight: "600",
  },
  input:{
    backgroundColor: "white",
    paddingVertical: 15,
    paddingHorizontal: 20,
    borderRadius: 30,
    marginVertical: 20,
    fontSize: 18,
  },
  toDo:{
    backgroundColor:theme.grey,
    marginBottom : 10,
    paddingVertical: 20,
    paddingHorizontal: 20,
    borderRadius: 15,
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "flex-start",
    flex : 1
  },
  toDos:{
    color : "white",
    fontSize : 17,
    fontWeight: "500",
    flex : 3
  },
  toDoSuc:{
    color : theme.bg,
    fontSize : 17,
    fontWeight: "500",
    textDecorationLine : "line-through",
    flex : 3
  },
  icon:{
    paddingRight : 8
  },
  changeText : {
    color : "black",
    backgroundColor : "white",
    flex : 3,
    marginRight : 10
  }
});
